




import { Component, Mixins, Prop, Watch, Vue } from "vue-property-decorator";

const prefix = "my-icon";
@Component({ name: "MyIcon" })
export default class MyIcon extends Vue {
    @Prop()
    name!: string;
    // 反馈的icon
    @Prop()
    diff!: string;
    // 是否在点击之后显示反馈的icon
    @Prop({ type: Boolean, default: false })
    feedback!: boolean;

    @Prop({ default: "medium" })
    size!: string;
    // 是否展示返回
    showDefault: boolean = true;
    get defaultName() {
        return `${prefix}-${this.name}`;
    }
    get feedbackName() {
        return `${prefix}-${this.diff}`;
    }
    get fontSize() {
        const size_map: Record<string, number> = {
            medium: 16,
            small: 14,
            mini: 12
        };
        return `${size_map[this.size]}px`;
    }
    click(event: any) {
        if (this.feedback) {
            this.showDefault = false;
            setTimeout(() => {
                this.showDefault = true;
            }, 1500);
        }
        this.$emit("click", event);
    }
}
